import React from 'react'
import { Navigate, Route,Routes,BrowserRouter,useParams,Outlet  } from 'react-router'
import { useNavigate } from 'react-router-dom'
import { Container } from 'reactstrap';
import App from './App'
import IndexPage from './Component/IndexPage'
import LoginPage from './Component/LoginPage'
import UserPage from './Component/UserPage/UserPage'

import Undev from './Component/Undev'
import Auth from './Auth/Auth'
import HomePage from './Component/HomePage'

import Empolyers from './Component/EmpolyerPage/Empolyers'

import NoEmpolyerBIOdata from './Component/StudentPage/NoEmpolyerBIOdata'

import Students from './Component/StudentPage/Students'
import StudentPage from './Component/StudentPage/StudentPage'
import DocumentPage from './Component/ProcessingPage/DocumentPage'
import TrainingGradePage from './Component/StudentPage/TrainingGradePage'
import PaymentPage from './Component/PaymentPage/PaymentPage'

import Processing from './Component/ProcessingPage/Processing'
import SchedulePage from './Component/ProcessingPage/SchedulePage'
import AirTicketPage from './Component/ProcessingPage/AirTicketPage'
import BackOutPage from './Component/ProcessingPage/BackOutPage'

import Training from './Component/TrainingPage/Training'

import Payment from './Component/PaymentPage/Payment'

import IntTest from './Component/IntTest'
import { useAuth0 } from "@auth0/auth0-react";
const auth = new Auth();

const handleAuthentication = ({ location }) => {
  if (/access_token|id_token|error/.test(location.hash)) {
    auth.handleAuthentication();
  }
}

export const makeMainRoutes = () => (
  <Container>
    <BrowserRouter>
    <Routes basename="/">
        <Route path="/"  element={<App auth={auth}/>}/>
        <Route path="/IndexPage" element={<IndexPage auth={auth}/>}/>
        <Route path="/HomePage" element={<HomePage />}/>
        <Route path="/Log_in" element={<LoginPage auth={auth}/>} />
        <Route path="/Undev" element={<Undev auth={auth}/>} />

        <Route path="/Empolyers" element={!auth.isAuthenticated() ? <Navigate to="/HomePage"/>
          : <Empolyers auth={auth}/>} />

        <Route path="/NoEmpolyerBIOdata" element={
          <NoEmpolyerBIOdata auth={auth}/>} />

        <Route path="/UserPage" element={ !auth.isAuthenticated() ? <Navigate to="/HomePage" />
          : <UserPage auth={auth}/>} />

        <Route path="/Students" element={ !auth.canUseStudent() ? <Navigate to="/HomePage" />

          : <Students auth={auth}/>} />

        <Route path="/StudentPage/:id" element={ !auth.canUseStudent() ? <Navigate to="/HomePage" />
          : <StudentPage auth={auth}id={props.match.params.id} />} />
        <Route path="/DocumentPage/:id" element={ !auth.canUseProcessing() ? <Navigate to="/HomePage" />
          : <DocumentPage auth={auth}id={props.match.params.id} />} />
        <Route path="/TrainingGradePage/:id" element={ !auth.canUseTraining() ? <Navigate to="/HomePage" />
          : <TrainingGradePage auth={auth}id={props.match.params.id} />} />
        <Route path="/PaymentPage/:id" element={ !auth.canUseAccounting() ? <Navigate to="/HomePage" />
          : <PaymentPage auth={auth}id={props.match.params.id} />} />

        <Route path="/Processing" element={ !auth.canUseProcessing() ? <Navigate to="/HomePage" />
          : <Processing auth={auth}/>} />

        <Route path="/SchedulePage/:id" element={ !auth.isAuthenticated() ? <Navigate to="/HomePage" />
          : <SchedulePage auth={auth}id={props.match.params.id} />} />
        <Route path="/BackOutPage/:id" element={ !auth.isAuthenticated() ? <Navigate to="/HomePage" />
          : <BackOutPage auth={auth}id={props.match.params.id} />} />
        <Route path="/AirTicketPage/:id" element={ !auth.isAuthenticated() ? <Navigate to="/HomePage" />
          : <AirTicketPage auth={auth}id={props.match.params.id} />} />

        <Route path="/Training" element={ !auth.canUseTraining() ? <Navigate to="/HomePage" />
          : <Training auth={auth}/>} />

        <Route path="/Payment" element={ !auth.canUseAccounting() ? <Navigate to="/HomePage" />
          : <Payment auth={auth}/>} />

        <Route path="/IntTest" element={ <IntTest auth={auth}/>} />
    </Routes>
    </BrowserRouter>
  </Container>
);
